import React, { FunctionComponent } from 'react';
import DemoContainer, { Demo } from './DemoContainer';
import useProximtiyFeedback from '../../src/useProximityFeedback';

const code = `<ProximityFeedback>
  {({ ref }) => <button ref={ref}>First example</button>}
</ProximityFeedback>
`;

const codeHook = `const Demo: FunctionComponent = () => {
  const { ref } = useProximtiyFeedback<HTMLButtonElement>();
  return (<button ref={ref}>First example</button>)
}`;

const DemoTypescript: FunctionComponent<Demo> = ({ number }) => {
  const { ref } = useProximtiyFeedback<HTMLButtonElement>();

  return (
    <DemoContainer
      number={number}
      heading="typescript"
      name="demo-typescript"
      description="When using the useProximityFeedback hook with TypeScript you need to specify the type of the ref component."
      code={code}
      codeHooks={codeHook}
    >
      <div>
        <button ref={ref}>See code above</button>
      </div>
    </DemoContainer>
  );
};

export default DemoTypescript;
